<template>
    <div>
        <div class="row">
            <div class="col-md-6">
                <h3>关于我</h3>
                <p>星光不问赶路人，时光不负有心人。这个世界上真的有人在过着你想要的生活。而那些人大都曾隐忍过你尚未经历的挫折。</p>
                <img src="../assets/About/about.jpg"/>
            </div>
            <div class="col-md-1"></div>
            <div class="col-md-5">
                <h3>特长</h3>
                <p>开发语言掌握程度</p>
                <div v-for="item in skill" class="skill">
                    <h4 class="row">
                        <span class="col-md-9">{{item.title}}</span>
                        <span class="col-md-3 shu">{{item.value}}%</span>
                    </h4>
                    <div class="progress">
                        <div class="progress-bar" role="progressbar" :style="{'width':item.value+'%'}">
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "About",
        data(){
            return{
                skill:[
                    {
                        title:'Vuejs',
                        value:60
                    },
                    {
                        title:'javascript',
                        value:95
                    },
                    {
                        title:'jquery',
                        value:89
                    },
                    {
                        title:'nodejs',
                        value:45
                    },
                    {
                        title:'bootstrap',
                        value:95
                    },
                    {
                        title:'element-ui',
                        value:55
                    },
                    {
                        title:'html5',
                        value:98
                    },
                    {
                        title:'css',
                        value:96
                    },
                    {
                        title:'Echarts',
                        value:60
                    },
                    {
                        title:'Bmap',
                        value:45
                    },
                    {
                        title:'Sqlsever',
                        value:40
                    }
                ]
            }
        }
    }
</script>

<style scoped>
img{
    width:100%;
    height: auto;
}
.progress{
    margin: 1rem auto;
    height: 0.571rem;
}
.progress-bar{
    background-color: #56c553;
}
.shu{
    text-align: right;
}
</style>



